/* -----------------------------------------------------------------------------
 * Ce fichier contient toutes les styles CSS de la barre de navigation
 * -----------------------------------------------------------------------------
 */

/* Style de la barre latérale */



.sidebar {
  position: fixed;
  left: 0;
  top: 0rem;
  height: 100vh;
  width: 7.8rem;
  background: #17181B;
  padding: .6rem 1.4rem;
  z-index: 9998;
  transition: all 0.5s ease;
 
}
/* Style de la barre latérale ouverte */
.sidebar.open {
  width: 25rem;

  
}


/* Style du logo et détails de la barre latérale */
.sidebar .logo-details {
  height: 6rem;
  display: flex;
  align-items: center;
  position: relative;
}

/* Style de l'icône du logo */
.sidebar .logo-details .icon {
  opacity: 0;
  transition: all 0.5s ease;
}

/* Style du nom du logo */
.sidebar .logo-details .logo_name {
  color: var(--accent-color);
  font-size: 2.5rem;
  font-weight: 900;
  opacity: 0;
  transition: all 0.5s ease;
  
}

/* Style de l'icône et du nom du logo lorsque la barre latérale est ouverte */
.sidebar.open .logo-details .icon,
.sidebar.open .logo-details .logo_name {
  opacity: 1;
}

/* Style du bouton de la barre latérale */
.sidebar .logo-details #btn {
  position: absolute;
  margin-top: 6rem;
  right: 0;
  transform: translateY(-50%);
  font-size: 2.2rem;
  transition: all 0.4s ease;
  font-size: $font-md;
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;


}

/* Style du bouton lorsque la barre latérale est ouverte */
.sidebar.open .logo-details #btn {
  text-align: right;
}

/* Style des icônes de la barre latérale */
.sidebar i {
  color: #fff;
  height: 6rem;
  min-width: 50px;
  font-size: $font-lg;
  text-align: center;
  line-height: 6rem;
  
}

/* Style de la liste de navigation de la barre latérale */
.sidebar .nav-list {
  margin-top: 2rem;
  height: 100%;
}

/* Style des éléments de la liste */
.sidebar li {
  position: relative;
  margin: .8rem 0rem;
  list-style: none;
}

/* Style des infobulles */
.sidebar li .tooltip {
  position: absolute;
  top: -2rem;
  left: calc(100% + 15px);
  z-index: 3;
  background: $lightGray;
  color: $darkGrey;
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.3);
  padding: .4rem .8rem;
  border-radius: .4rem;
  font-size: 1.2rem;
  font-weight: 400;
  opacity: 0;
  white-space: nowrap;
  pointer-events: none;
  transition: 0s;
}
/* Style des infobulles au survol */
.sidebar li:hover .tooltip {
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
  top: 50%;
  transform: translateY(-50%);
}

/* Style des infobulles lorsque la barre latérale est ouverte */
.sidebar.open li .tooltip {
  display: none;
}

/* Ajout d'un caret à gauche du tooltip */
.sidebar li .tooltip:before {
  content: "";
  position: absolute;
  top: 50%;
  left: -8px; /* distance entre le caret et le tooltip */
  margin-top: -5px; /* ajustement pour centrer le caret */
  border-top: 5px solid transparent;
  border-right: 8px solid $lightGray; /* couleur de fond du tooltip */
  border-bottom: 5px solid transparent;
}

/* Style des inputs de la barre latérale */
.sidebar input {
  font-size: 15px;
  color: $White;
  font-weight: 400;
  outline: none;
  height: 5rem;
  width: 100%;
  width: 5rem;
  border: none;
  border-radius: 1.2rem;
  transition: all 0.5s ease;
  background: $darkGrey;
}

/* Style des inputs lorsque la barre latérale est ouverte */
.sidebar.open input {
  padding: 0 2rem 0 5rem;
  width: 100%;
}

/* Style des liens de la liste de navigation */
.sidebar li a {
  display: flex;
  height: 100%;
  width: 100%;
  border-radius: 1.2rem;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s ease;
  background: $darkGrey;

  
}

/* Style des liens de la liste de navigation au survol */
.sidebar li a:hover {
  background: $White;
}

/* Style des noms de liens de la liste de navigation */
.sidebar li a .links_name {
  color: $White;
  font-size: 1.5rem;
  font-weight: 400;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
}

/* Style des noms de liens lorsque la barre latérale est ouverte */
.sidebar.open li a .links_name {
  opacity: 1;
  pointer-events: auto;
}

/* Style des noms de liens et icônes de la liste de navigation au survol */
.sidebar li a:hover .links_name,
.sidebar li a:hover i {
  transition: all 0.5s ease;
  color: $darkGrey;
}

/* Style des icônes de la liste de navigation */
.sidebar li i {
  height: 5rem;
  line-height: 5rem;
  font-size: $font-sm;
  border-radius: $font-title-xx;
}

/* Style du profil dans la barre latérale */
.sidebar li.profile {
  position: fixed;
  height: 6rem;
  width: 7.8rem;
  left: 0;
  bottom: -.8rem;
  padding: 1rem 1.4rem;
  transition: all 0.5s ease;
  overflow: hidden;
}
/* Style du profil lorsque la barre latérale est ouverte */
.sidebar.open li.profile {
  width: 25rem;
  margin-bottom: 3rem;
}

/* Style des détails du profil dans la barre latérale */
.sidebar li .profile-details {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: center;
  text-align: center;
  
  
}

/* Style de l'image du profil */
.sidebar li img {
  height: 4.5rem;
  width: 4.5rem;
  object-fit: cover;
  border-radius: 6px;
  margin-right: 1rem;
}

/* Style du nom et du métier du profil */
.sidebar .nav-list li.profile .name,
.sidebar .nav-list li.profile .job {
  font-size: 15px;
  font-weight: 400;
  color: $White;
  opacity: 0;
  white-space: nowrap;
  
}

.sidebar.open li.profile .name,
.sidebar.open li.profile .job {
  font-size: 15px;
  font-weight: 400;
  color: $titleColor;;
  opacity: 1;
  white-space: nowrap;
  
 
}


/* Style du métier du profil */
.sidebar li.profile .job {
  font-size: 12px;
  
}



/* Style de la section principale */
.home-section {
  left: 7.8rem;
  transition: all 0.5s ease;
}

/* Style de la section principale lorsque la barre latérale est ouverte */
.sidebar.open ~ .home-section {
  left: 25rem;
  width: calc(100% - 25rem);

  
  @include breakpoint-down(small) {
    left: 0;
    width: 100%;
}
}

/* Style du texte dans la section principale */
.home-section .text {
  display: inline-block;
  color: $verylightgray;
  font-size: $font-lg;
  font-weight: 500;
  margin-right: 1.8rem;
  @include breakpoint-down('medium') {

    margin-right: .1rem;
  }
}


/* Style du contenu principal lorsque la barre latérale est ouverte */
.sidebar.open + main {
  margin-left: 16rem;
}

/* Style du menu déroulant */
#menu.container + .sidebar.open {
  margin-left: 25rem;
}

/* Styles pour les sections */
section {
  transition: all 0.5s ease;
}

/* Styles pour les sections lorsque la barre latérale est ouverte */
.sidebar.open section {
  margin-left: 17.2rem;
}

/* Styles pour les titres de section */
.title {
  transition: all 0.5s ease;
}

/* Styles pour les titres de section lorsque la barre latérale est ouverte */
.sidebar.open .title {
  margin-left: 17.2rem;
}

/* Styles pour les sections avec une classe fade-in */
.fade-in {
  opacity: 0;
  transform: translateX(-20px);
  transition: all 0.5s ease;
}

/* Styles pour les sections avec une classe fade-in lorsque la barre latérale est ouverte */
.sidebar.open .fade-in {
  opacity: 1;
  transform: none;
}
/* Styles pour le nom du logo */
.logo_name {
  border: $White solid .2rem;
  border-radius: 100%;
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Styles pour l'animation de rotation */
.rotate {
  animation-name: rotation;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-iteration-count: 1;
}

/* Définition de l'animation de rotation */
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/* Styles par défaut pour tous les navigateurs */
.sidebar .nav-list .social-icons {
  display: flex;
  justify-content: center;
  margin-top: 28vh;
}

/* Styles spécifiques pour Firefox */
@supports (-moz-appearance: none) {
  .sidebar .nav-list .social-icons {
    margin-top: 33vh;
  }
}

/* Styles spécifiques pour Chrome */
@supports (-webkit-appearance: none) {
  .sidebar .nav-list .social-icons {
    margin-top: 28vh;
  }
}

.sidebar .nav-list .social-icons a {
  color: #fff;
}

.sidebar .nav-list .social-icons i {
  font-size: 2rem;
  opacity: 0;
  pointer-events: none;
  transition: 0.4s;
  min-width: 4rem ;
  color: $titleColor;
}
.sidebar .nav-list .social-icons i:hover {

  color: var(--accent-color);
}
.sidebar.open .nav-list .social-icons i {
  opacity: 1;
  pointer-events: auto;
}

@include breakpoint-down('small') {

  .sidebar {
    
    width: 100%;
    height: 5.5rem;
    bottom: auto;
    top: 0;
    position: sticky;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .sidebar{
    background-color:transparent;
    transition: background-color 1s ease, box-shadow 1s ease;
  }

  .sidebar.open {
    background: #17181B;
    width: 100%; /* Fait que le menu occupe 100% de la largeur de l'écran */
    height: 100vh; /* Fait que le menu occupe 100% de la hauteur de l'écran */
    position: fixed;
    top: 0;
    left: 0; /* Assure que le menu commence à gauche de l'écran */
    overflow: hidden;

    transition: all 0.5s ease;
    flex-direction: column;
  }

  .sidebar .logo-details {
    height: 3.5rem;
  }

  .sidebar .nav-list {
    display: none;
  }

  .sidebar.open .nav-list {
    display: block;
    margin-top: 30%;
  }

  .sidebar li.profile {
    position: static;
    height: auto;
    width: auto;
    padding: 0;
  }

  .sidebar .nav-list .social-icons {
    position: fixed;
  
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    margin-top: 1vh;
  }
  

  .sidebar.open .logo-details #btn{
    position: fixed;
    margin-top: 8rem;
    left: 10px;
    margin-right: 20px;
    z-index: 999;

  }
  
  .sidebar #btn {
    display: fixed;
    left: 0;
    position: absolute;
    right: 1rem;
  }

  .fixed-icon {
    position: fixed;
    left: 15px;  /* adjust as needed */
   top: 15px;  /* adjust as needed */
    font-size: 20px;
    text-decoration: none;
    z-index: 1000;
  }

  .sidebar .logo-details .logo_name{
    margin-top:40px;
    margin-left: -150px;
    left:0;

  }

  .icon-linkedin {
  //  top: auto;
  //  bottom: calc(3 * 24px + 3 * 20px);  /* adjust as needed */
    color: $lightGray;
  }

  .icon-github {
    top: auto;
    bottom: calc(2 * 24px + 2 * 20px);  /* adjust as needed */
    color: $lightGray;
  }

  .icon-facebook {
    top: auto;
    bottom: calc(1 * 24px + 1 * 20px);  /* adjust as needed */
    color: $lightGray;
  }
 .nav-hidden {
    transform: translateY(-100%);
    transition: transform 0.9s ease;
  }

  .nav-show {
    background: #17181B;
    transition: all 0.5s ease; /* Transition fluide pour l'effet de défilement */
  }
  .sidebar.nav-scrolled {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .sidebar.nav-hidden {
    box-shadow: none; /* Ombre supprimée */
    background-color: transparent; /* Fond transparent */
  }
}